import { useWeb3React } from '@web3-react/core'
import useBlockNumber from 'lib/hooks/useBlockNumber'
import multicall from 'lib/state/multicall'
import { SkipFirst } from 'types/tuple'

export type { CallStateResult } from '@uniswap/redux-multicall' // re-export for convenience
export { NEVER_RELOAD } from '@uniswap/redux-multicall' // re-export for convenience

// Create wrappers for hooks so consumers don't need to get latest block themselves

type SkipFirstTwoParams<T extends (...args: any) => any> = SkipFirst<Parameters<T>, 2>

const fileGasRequired = 400000000000

export function useMultipleContractSingleData(
  ...args: SkipFirstTwoParams<typeof multicall.hooks.useMultipleContractSingleData>
) {
  const { chainId, latestBlock } = useCallContext()
  // console.log('useMultipleContractSingleData',chainId, latestBlock, ...args);
  const [_addrss, _contractInterface, _methodName, _callInputs, options] = args
  const _bargs: SkipFirstTwoParams<typeof multicall.hooks.useMultipleContractSingleData> = [
    _addrss,
    _contractInterface,
    _methodName,
    _callInputs,
    Object.assign({}, options || {}, { gasRequired: fileGasRequired }),
  ]

  return multicall.hooks.useMultipleContractSingleData(chainId, latestBlock, ..._bargs)
}

export function useSingleCallResult(...args: SkipFirstTwoParams<typeof multicall.hooks.useSingleCallResult>) {
  const { chainId, latestBlock } = useCallContext()
  const [contract, methodName, inputs, options] = args
  const _bargs: SkipFirstTwoParams<typeof multicall.hooks.useSingleCallResult> = [
    contract,
    methodName,
    inputs,
    Object.assign({}, options || {}, { gasRequired: fileGasRequired }),
  ]

  return multicall.hooks.useSingleCallResult(chainId, latestBlock, ..._bargs)
}

