export function useSingleContractWithCallData(
  ...args: SkipFirstTwoParams<typeof multicall.hooks.useSingleContractWithCallData>
) {
  const { chainId, latestBlock } = useCallContext()

  const [_contract, _callInputs, options] = args
  const _bargs: SkipFirstTwoParams<typeof multicall.hooks.useSingleContractWithCallData> = [
    _contract,
    _callInputs,
    Object.assign({}, options || {}, { gasRequired: fileGasRequired }),
  ]

  return multicall.hooks.useSingleContractWithCallData(chainId, latestBlock, ..._bargs)
}

function useCallContext() {
  const { chainId } = useWeb3React()
  const latestBlock = useBlockNumber()
  return { chainId, latestBlock }
}
